#ModalView{top:0;left:0;position:fixed;width:100vw;height:100vh;background:rgba(22,29,30,0.8);justify-content:center;display:none;z-index:1200}#ModalView article#container{display:flex;flex-direction:column;justify-content:center;padding:20px}#ModalView article#container section#main{background:white;width:400px;min-height:200px;height:auto;box-shadow:-5px 13px 19px #2c393b}#ModalView article#container section#main header{overflow:hidden}#ModalView article#container section#main header img{width:calc(100% + 2px);height:auto;margin-top:-2px;margin-left:-1px}#ModalView article#container section#main header h3{font-family:"Lato";padding:20px 20px}#ModalView article#container section#main section{font-family:"Lato";padding:0px 20px}#ModalView article#container section#main footer{padding:20px;display:flex;justify-content:flex-end}#ModalView article#container section#main footer button{background:none;font-family:"Lato";font-weight:bold;border:none;text-transform:uppercase;font-size:13px;padding:2px 0;margin:0  0 0 30px;border-bottom:2px solid #fff}#ModalView article#container section#main footer button.success{color:#2257f5}#ModalView article#container section#main footer button.success:hover{border-bottom:2px solid #2257f5}#ModalView article#container section#main footer button.neutral{color:#818a91}#ModalView article#container section#main footer button.neutral:hover{border-bottom:2px solid #161d1e;color:#161d1e}@media (max-width: 767px){#ModalView article#container section#main{width:100%;min-height:180px;height:auto}#ModalView article#container section#main header h3{font-size:1.25rem}#ModalView article#container section#main section{font-size:.75rem}}
